import * as React from 'react'
import { Container } from 'reactstrap'
import './Main.css'

interface ExperienceProps {
  duration: string
  title: string
  company: string
  description: string
}

class Experience extends React.Component<ExperienceProps, {}> {
  render() {
    let { duration, title, company, description } = this.props

    return (
      <div>
        <h5><strong>{title}</strong></h5>
        <div>{company} <span className="duration-time">[{duration}]</span> </div>
        {/* <div className="black-light">{duration}</div> */}
        <div className="medium-text" dangerouslySetInnerHTML={{ __html: description }} />
        <div>-----</div>
      </div>
    )
  }
}

interface SkillProps {
  header: string
  description: string
}

class Skill extends React.Component<SkillProps, {}> {
  render() {
    let { header, description } = this.props

    return (
      <div>
        <h5>{header}</h5>
        <div className="medium-text" dangerouslySetInnerHTML={{ __html: description }} />
        <div>-----</div>
      </div>
    )
  }
}

interface SimpleProps {
  header: string
  description: string
}

class Simple extends React.Component<SimpleProps, {}> {
  render() {
    let { header, description } = this.props

    return (
      <div>
        <h1>{header}</h1>
        <div dangerouslySetInnerHTML={{ __html: description }} />
      </div>
    )
  }
}

